<script>
export default {
    data: function(){
        return {
            date: new Date().toISOString().substring(0, 7),
        }
    },
    created: function(){
        this.get();
    },
    watch: {
        date: {
            handler: function(){
                this.get();
            }
        }
    },
    methods:{
        get: function(){
            axios.post('/report/get-monthly-plan',{date: this.date})
                .then(res=>{
                    // console.log(res.data);
                    $('#plan_form table input').val('');
                    $('#plan_form table textarea').val('');
                    $('#plan_form table input[type="checkbox"').prop('checked',false);
                    let data = res.data;
                    for (const key in data) {
                        if (Object.hasOwnProperty.call(data, key)) {
                            const element = data[key];
                            $(`input[name="${key}"]`).val(element);
                        }
                    }
                })
        },
        set: function(event){
            let form_data = new FormData(event.target);
            axios.post('/report/set-monthly-plan',form_data)
                .then(res=>{
                    // console.log(res.data);
                    window.s_alert('plan updated','success')
                })
        },
        print_report: function(){
            window.print();
        },
    }
}
</script>

